import React, { useEffect, useRef, useState } from "react";
import styles from "../scss/components/SortButtons.module.scss";
import { useDispatch, useSelector } from "react-redux";
import {
  setSortType,
  setOrderType,
  selectFilter, SortTypeType,
} from "../redux/slices/filterSlice";

export const sortList: SortTypeType[] = [
  { name: "", sortProperty: "rating" },
  { name: "", sortProperty: "price" },
  { name: "", sortProperty: "title" },
];

const Sort: React.FC = React.memo(() => {
  const [open, setOpen] = useState(false);
  const { sortType } = useSelector(selectFilter);
  const orderTypes = ["asc", "desc"];
  const dispatch = useDispatch();
  const sortRef = useRef<HTMLDivElement>(null);

  const onClickListItem = (newSortType: SortTypeType) => {
    dispatch(setSortType(newSortType));
    setOpen(false);
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (sortRef.current && !event.composedPath().includes(sortRef.current)) {
        setOpen(false);
      }
    };

    document.body.addEventListener("click", handleClickOutside);

    return () => document.body.addEventListener("click", handleClickOutside);
  }, []);
  return (
      <div ref={sortRef} className="sort">
        <div className="sort__label">
          <svg
              width="10"
              height="6"
              viewBox="0 0 10 6"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
          >
            <path
                d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
                fill="#2C2C2C"
            />
          </svg>
          <b> :</b>
          <span onClick={() => setOpen(!open)}>{sortType.name}</span>
        </div>
        {open && (
            <div className="sort__popup">
              <ul>
                {sortList.map((obj, i) => (
                    <li
                        onClick={() => onClickListItem(obj)}
                        className={
                          sortType.sortProperty === obj.sortProperty ? "active" : ""
                        }
                        key={i}
                    >
                      {obj.name}
                    </li>
                ))}
              </ul>
            </div>
        )}
        <div className={styles.root}>
          <button onClick={() => dispatch(setOrderType(orderTypes[0]))}></button>
          <button onClick={() => dispatch(setOrderType(orderTypes[1]))}></button>
        </div>
      </div>
  );
})

Sort.displayName = 'Sort'

export default Sort;
